.nav{
    grid-area:n;
    background-color: cornflowerblue;
    padding: 20px;  /*расстоние от ссылок (рамка)*/
}
.item a {
    color: white; /*дает ссылкам белый цвет*/
    text-decoration: none;  /*чтобы у ссылок не было подчеркиваний*/
}
.item a.activeLink {  /*ссылка с классом есктив будет золотая*/
    color: gold;
}
.items a {
    color: gold; /*дает ссылкам белый цвет*/
    text-decoration: none;  /*чтобы у ссылок не было подчеркиваний*/
}
